.toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
}
@media only screen and (max-width: 70em) {
  .toggle {
    width: 100%;
    justify-content: space-between;
  }
}
@media only screen and (max-width: 40em) {
  .toggle {
    height: 60%;
  }
}
.toggle label > .text-label {
  width: 100%;
  min-width: 4.4375rem;
  font-weight: 700;
  font-size: var(--fz-labeltext);
  color: var(--clr-labeltext-theme);
  opacity: var(--theme-label);
}
@media only screen and (max-width: 70em) {
  .toggle label > .text-label {
    --fz-labeltext: 0.625rem;
  }
}
@media only screen and (min-width: 40em) and (max-width: 70em) {
  .toggle label > .text-label {
    text-align: right;
  }
}
.toggle > .wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem 0;
  margin-left: 1rem;
  width: 100%;
  max-width: 2.75rem;
}
@media only screen and (max-width: 70em) {
  .toggle > .wrapper {
    margin-left: 0;
  }
}
.toggle > .wrapper > input {
  width: 18px;
  height: 18px;
}
.toggle > .wrapper > input:nth-child(2) {
  margin-left: 2px;
}
.toggle > .wrapper > .background {
  position: absolute;
  transform: translate(-50%);
  display: block;
  left: 50%;
  width: 100%;
  height: 24px;
  background: var(--clr-background-toggle);
  border-radius: 20px;
  opacity: 0.5;
  pointer-events: none;
}
.toggle > .wrapper > .btn {
  z-index: 2;
  position: absolute;
  transform: translateY(-50%);
  display: block;
  top: 50%;
  left: 0%;
  width: 18px;
  height: 18px;
  margin-left: 3px;
  border-radius: 20px;
  background-color: var(--clr-button-toggle);
  transition: left 400ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  pointer-events: none;
  opacity: 0.8;
}
.toggle > .wrapper > .btn:hover {
  left: 46%;
}

.card {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 1rem 3rem;
  border-radius: 0.3125rem;
  background: var(--clr-background-card);
}
.card > :nth-child(even) {
  text-align: right;
}
.card:hover {
  cursor: pointer;
  background: var(--clr-activebackground-card);
}

.cardgrande {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1.75rem 0 1.25rem 0;
  border-radius: 0.3125rem;
  color: #63687e;
  background: var(--clr-background-cardgrande);
}
.cardgrande::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 4px;
  background: var(--clr-basetext);
}
.cardgrande:hover {
  cursor: pointer;
  background: var(--clr-activebackground-cardgrande);
}
.cardgrande > .redesocial {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cardgrande > .redesocial > .icone {
  font-size: 1.25rem;
  color: var(--clr-basetext);
  margin-right: 0.5rem;
}
.cardgrande > .redesocial > .tag {
  color: #63687e;
}
.cardgrande > .numero {
  margin-top: 1.125rem;
  font-size: var(--fz-650);
  text-align: center;
}
.cardgrande > .texto {
  margin-top: 0.25rem;
  color: var(--clr-basetext);
  font-size: var(--fz-50);
  font-weight: 400;
  text-align: center;
  letter-spacing: 4px;
  text-transform: uppercase;
}
.cardgrande > .crescimento {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.375rem;
}
.cardgrande > .crescimento.positivo > * {
  color: #1db489;
}
.cardgrande > .crescimento.negativo > * {
  color: #dc414c;
}
.cardgrande > .crescimento > .icone.flecha {
  width: 8px;
  height: 4px;
  margin-right: 0.1rem;
  transform: translateY(-25%);
}
.cardgrande > .crescimento > .icone.flecha.-cima {
  background: url(../images/icon-up.svg);
}
.cardgrande > .crescimento > .icone.flecha.-baixo {
  background: url(../images/icon-down.svg);
}
.cardgrande > .crescimento > .numero {
  padding-right: 0.1rem;
}
.cardgrande > .crescimento > :nth-child(even) {
  text-align: left;
}

.cardgrande.facebook::before {
  background: #198ff5;
}
.cardgrande.facebook > .redesocial > .icone.facebook {
  width: 20px;
  height: 20px;
  background: url(../images/icon-facebook.svg);
}

.cardgrande.twitter::before {
  background: #1ca0f2;
}
.cardgrande.twitter > .redesocial > .icone.twitter {
  width: 21px;
  height: 17px;
  background: url(../images/icon-twitter.svg);
}

.cardgrande.instagram::before {
  background: linear-gradient(90deg, var(--clr-primarylinear-100));
}
.cardgrande.instagram > .redesocial > .icone.instagram {
  width: 20px;
  height: 20px;
  background: url(../images/icon-instagram.svg);
}

.cardgrande.youtube::before {
  background: #c4032a;
}
.cardgrande.youtube > .redesocial > .icone.youtube {
  width: 20px;
  height: 20px;
  background: url(../images/icon-youtube.svg);
}

.dashboard > .resumo > .titulo {
  font-size: var(--fz-titulo-dashboard-resumo);
  color: var(--clr-lighttheme-500);
}
.dashboard > .resumo > .case-cartoes {
  display: grid;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  width: auto;
  align-self: center;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(8, 1fr);
}
.dashboard > .resumo > .case-cartoes > * {
  width: 100%;
}
@media only screen and (min-width: 40em) {
  .dashboard > .resumo > .case-cartoes {
    align-self: center;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(4, 1fr);
    width: auto;
  }
}
@media only screen and (min-width: 70em) {
  .dashboard > .resumo > .case-cartoes {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr 1fr;
    width: 100%;
  }
}